<template>
  <div>
    <header>
      <h3>Hi there!</h3>
    </header>
    <h5>I am Karl. But you already know that.</h5>
    <h5>So here is a little bit about me:</h5>
    <p>I am 22 years old</p>
    <p>Currently finishing my secondary education</p>
    <p>I first touched web development a bit in primary school as part of the required curriculum,
      we did some basic HTML and CSS back then</p>
    <p>While HTML didn't particularly interest me, my friends and I quickly moved on to learning Python in our free time</p>
    <p>Python was much more interesting to me - it was about logic and not what colour some text should be or where it should be aligned</p>
    <p>After finishing primary school I started secondary school with the intention of becoming an engineer</p>
    <p>During secondary school I learned some more Python, but I also began to learn a little C++</p>
    <p>Unfortunately I didn't finish my secondary education back then</p>
